<script setup>
import { ref, computed, defineProps, defineOptions } from 'vue';
import { applyReactInVue } from 'veaury';

// Store
import { useAppStore } from '@/store/app/app.js';
const appStore = useAppStore();

const props = defineProps({
  card: {
    type: Object,
    required: true,
  },
  dateRange: {
    type: Array,
    required: true,
  },
});

/**
 * Checks if the user has the required capabilities
 * @param {Array} capabilities - The capabilities to check
 * @returns {Boolean}
 */
const userHasCapability = (capabilities) => {
  if (!capabilities) return true;
  if (!Array.isArray(capabilities)) return true;
  if (!appStore.state.currentUser) return false;

  // Check if allcaps is available (may be loading asynchronously)
  if (!appStore.state.currentUser.allcaps) return false;

  for (let capability of capabilities) {
    const hasCap = appStore.state.currentUser.allcaps[capability];
    if (hasCap) return true;
  }
  return false;
};

/**
 * Gets the className for the card based on metadata
 * @returns {string} The className string
 */
const cardClassName = computed(() => {
  return `col-span-${props.card.metadata.width || 4} max-md:col-span-${
    props.card.metadata.mobileWidth || 12
  }`;
});

/**
 * Gets the component to render, wrapping React components with veaury if needed
 * @returns {Object} The component to render (Vue component or veaury-wrapped React component)
 */
const cardComponent = computed(() => {
  const language = props.card?.metadata?.language || 'vue';

  // If it's a React component, wrap it with veaury
  if (language === 'react' && props.card.component) {
    return applyReactInVue(props.card.component);
  }

  // Otherwise, return the Vue component as-is
  return props.card.component;
});

/**
 * Checks if the card is a React component
 * @returns {boolean}
 */
const isReactCard = computed(() => {
  return props.card?.metadata?.language === 'react';
});

defineOptions({
  name: 'CardRender',
});
</script>

<template>
  <!-- Single Card -->
  <template v-if="!card.isGroup">
    <!-- Vue Card -->
    <component
      v-if="
        userHasCapability(card?.metadata?.requires_capabilities) && !isReactCard
      "
      :is="cardComponent"
      :date-range="dateRange"
      :app-data="appStore"
      :id="card.metadata.id"
      :class="cardClassName"
    />
    <!-- React Card - wrapped in div to apply grid classes to veaury wrapper -->
    <div
      v-if="
        userHasCapability(card?.metadata?.requires_capabilities) && isReactCard
      "
      :class="cardClassName"
    >
      <component
        :is="cardComponent"
        :dateRange="dateRange"
        :appData="appStore"
        :id="card.metadata.id"
      />
    </div>
  </template>

  <!-- Group Card -->
  <template v-else-if="card.isGroup">
    <div
      class="col-span-{{ card.metadata.width }} max-md:col-span-{{ card.metadata.mobileWidth }}"
    >
      <div class="grid grid-cols-{{ card.metadata.columns }} gap-6">
        <CardRender
          v-for="child in card.children"
          :key="child.metadata.id"
          :card="child"
          :date-range="dateRange"
        />
      </div>
    </div>
  </template>
</template>
