<script setup>
import { ref, computed, onMounted, onUnmounted, watch, nextTick } from 'vue';
import { ShadowRoot } from 'vue-shadow-dom';
import { useDarkMode } from './src/useDarkMode.js';
const { isDark } = useDarkMode();

// Configure veaury for React 19 support
import { setVeauryOptions } from 'veaury';
import { createRoot } from 'react-dom/client';
setVeauryOptions({
  react: {
    createRoot,
  },
});

// Import the hooks system
import { applyFilters } from '@/assets/js/functions/HooksSystem.js';

// Cards
import './src/cards/index.js';

// Store
import { useAppStore } from '@/store/app/app.js';
const appStore = useAppStore();

// Comps
import LoadingIndicator from '@/components/utility/loading-indicator/index.ts';
import Notifications from '@/components/utility/notifications/index.vue';
import AppIcon from '@/components/utility/icons/index.vue';
import Confirm from '@/components/utility/confirm/index.vue';
import AppToggle from '@/components/utility/toggle/index.vue';
import CardRender from './src/card-render.vue';

// Dashboard components
import DateRangePicker from './src/date-range-picker.vue';

// Refs
const adoptedStyleSheets = ref(new CSSStyleSheet());
const confirm = ref(null);

// Dashboard state
const dateRange = ref([
  new Date(new Date().setDate(new Date().getDate() - 7)),
  new Date(),
]);

const registeredCards = ref([]);
const registeredCategories = ref([]);
const activeCategory = ref(null);
const loading = ref(false);

const activeCategoryID = computed(() => {
  const urlParams = new URLSearchParams(window.location.search);
  const dashboardCategory = urlParams.get('dashboard_category');

  if (dashboardCategory) {
    const siteCategory = registeredCategories.value.find(
      (category) => category.value === dashboardCategory
    )?.value;
    if (siteCategory) return siteCategory;
  }

  if (registeredCategories.value[0]) return registeredCategories.value[0].value;
  return null;
});

const formattedCategories = computed(() => {
  const formattedCategories = {};
  registeredCategories.value.forEach((category) => {
    formattedCategories[category.value] = category;
  });
  return formattedCategories;
});

/**
 * Handle date range changes
 */
const handleDateRangeChange = (newDateRange) => {
  dateRange.value = newDateRange;
};

/**
 * Injects CSS styles into the shadow root
 */
const setStyles = () => {
  let appStyleNode = document.querySelector('#uixpress-dashboard-css');
  if (!appStyleNode) {
    appStyleNode = manuallyAddStyleSheet();
    appStyleNode.onload = () => {
      const appStyles = appStyleNode.sheet;
      for (const rule of [...appStyles.cssRules].reverse()) {
        adoptedStyleSheets.value.insertRule(rule.cssText);
      }
    };
  } else {
    const appStyles = appStyleNode.sheet;
    for (const rule of [...appStyles.cssRules].reverse()) {
      adoptedStyleSheets.value.insertRule(rule.cssText);
    }
  }
};

/**
 * Creates and adds a stylesheet link element to the document head
 */
const manuallyAddStyleSheet = () => {
  const link = document.createElement('link');
  link.rel = 'stylesheet';
  link.href = `${appStore.state.pluginBase}app/dist/assets/styles/dashboard.css`;
  document.head.appendChild(link);
  return link;
};

watch(
  registeredCategories,
  (newVal, oldVal) => {
    activeCategory.value = activeCategoryID.value;
  },
  { deep: true }
);

onMounted(async () => {
  setStyles();

  // Dispatch event for plugins to register cards
  const event = new CustomEvent('uixpress/dashboard/ready');
  document.dispatchEvent(event);

  await nextTick();

  // Add categories
  registeredCategories.value = await applyFilters(
    'uixpress/dashboard/categories/register',
    registeredCategories.value
  );

  console.log(registeredCategories.value);

  // Let other apps modify/add to the widgets array
  registeredCards.value = await applyFilters(
    'uixpress/dashboard/cards/register',
    registeredCards.value
  );

  console.log(registeredCards.value);
});

watch(activeCategory, (newVal, oldVal) => {
  if (newVal === oldVal) return;

  const urlParams = new URLSearchParams(window.location.search);
  urlParams.set('dashboard_category', newVal);
  window.history.replaceState(
    {},
    '',
    `${window.location.pathname}?${urlParams.toString()}`
  );
});
</script>

<template>
  <component is="style"> #wpcontent{padding:0}</component>
  <ShadowRoot
    tag="div"
    :adopted-style-sheets="[adoptedStyleSheets]"
    class="uipx-normalize"
  >
    <Notifications />
    <div class="translate-x-0">
      <LoadingIndicator :height="3" />
    </div>

    <template v-for="item in appStore.state.dashboard_data.styles">
      <link
        rel="stylesheet"
        :id="`${item.handle}-css`"
        :href="item.src"
        media="all"
      />
    </template>
    <div
      :class="isDark ? 'dark' : ''"
      class="border border-solid border-zinc-200/50 dark:border-zinc-700/30 rounded-3xl max-h-full overflow-auto bg-white dark:bg-zinc-800/20"
    >
      <div
        class="@container uipx-normalize font-sans p-8 flex flex-col gap-6 text-zinc-500 dark:text-zinc-400"
        style="min-height: calc(100dvh - var(--wp-admin--admin-bar--height))"
      >
        <!-- Dashboard Header -->
        <div class="flex flex-row justify-between items-center mb-3">
          <div>
            <h1 class="text-2xl font-semibold text-zinc-900 dark:text-zinc-100">
              {{ __('Dashboard', 'uixpress') }}
            </h1>
            <p class="text-sm text-zinc-500 dark:text-zinc-400 mt-1">
              {{ __('Overview of your site activity', 'uixpress') }}
            </p>
          </div>

          <div class="flex items-center gap-3">
            <!-- Date Range Picker -->
            <DateRangePicker
              :value="dateRange"
              @updated="handleDateRangeChange"
            />
          </div>
        </div>

        <div
          v-if="registeredCategories.length > 0"
          class="flex items-center mb-3"
        >
          <AppToggle
            :options="formattedCategories"
            v-model="activeCategory"
            class="w-auto mr-auto"
            style="width: auto"
          />
        </div>

        <!-- Dashboard Cards Grid -->
        <div v-if="registeredCards.length > 1" class="grid grid-cols-12 gap-10">
          <template v-for="card in registeredCards">
            <CardRender
              v-if="card.metadata.category === activeCategory"
              :key="card.metadata.id"
              :card="card"
              :date-range="dateRange"
            />
          </template>
        </div>

        <!-- Default content if no cards -->
        <div
          v-else
          class="bg-zinc-50 dark:bg-zinc-800/50 rounded-lg p-8 text-center"
        >
          <AppIcon
            icon="dashboard"
            class="text-4xl text-zinc-400 mx-auto mb-4"
          />
          <h3 class="text-lg font-medium text-zinc-900 dark:text-zinc-100 mb-2">
            {{ __('No Dashboard Cards', 'uixpress') }}
          </h3>
          <p class="text-zinc-500 dark:text-zinc-400">
            {{
              __(
                'Install plugins or extensions to add dashboard cards here.',
                'uixpress'
              )
            }}
          </p>
        </div>

        <Confirm ref="confirm" />
      </div>
    </div>
  </ShadowRoot>
</template>
