<script setup>
import { ref, computed, onMounted, watch, nextTick } from 'vue';
import { Line } from 'vue-chartjs';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler,
} from 'chart.js';
import AppIcon from '@/components/utility/icons/index.vue';
import { useAnalyticsQueue } from '@/composables/useAnalyticsQueue.js';

// Initialize analytics queue
const { analytics: analyticsQueue } = useAnalyticsQueue();

// Register Chart.js components
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler
);

const props = defineProps({
  dateRange: {
    type: Object,
    required: true,
  },
  appData: {
    type: Object,
    required: true,
  },
});

// Refs
const analytics = ref(null);
const loading = ref(false);
const error = ref(null);
const chartRef = ref(null);
const chartKey = ref(0);

/**
 * Load page views analytics data using queue
 */
const loadAnalytics = async () => {
  loading.value = true;
  error.value = null;

  try {
    // Format dates for API (ISO 8601 format)
    const startDate = props.dateRange[0].toISOString();
    const endDate = props.dateRange[1].toISOString();

    // Fetch chart data and overview stats using queue
    const [chartData, overviewData] = await Promise.all([
      analyticsQueue.getChart(startDate, endDate, 'both', {
        cacheMaxAge: 30000, // 30 seconds cache
      }),
      analyticsQueue.getOverview(startDate, endDate, null, {
        cacheMaxAge: 30000, // 30 seconds cache
      }),
    ]);

    if (chartData && overviewData) {
      analytics.value = {
        ...overviewData,
        chart_data: chartData,
      };
    } else {
      analytics.value = null;
    }

    // Force chart re-render by updating the key
    chartKey.value++;
  } catch (err) {
    error.value = err.message || 'Failed to load analytics data';
    console.error('Analytics error:', err);
  } finally {
    loading.value = false;
  }
};

/**
 * Format date label for chart using browser locale
 */
const formatDateLabel = (date) => {
  // Handle both Date objects and date strings from API
  const dateObj = typeof date === 'string' ? new Date(date) : date;

  // Get browser locale
  const locale = navigator.language || 'en-US';

  // Format based on locale - use shorter format for chart labels
  try {
    // For US locale, use M/D format
    if (locale.startsWith('en-US')) {
      const month = dateObj.getMonth() + 1;
      const day = dateObj.getDate();
      return `${month}/${day}`;
    }

    // For other locales, use locale-specific short date format
    return dateObj.toLocaleDateString(locale, {
      month: 'numeric',
      day: 'numeric',
    });
  } catch (error) {
    // Fallback to simple format if locale formatting fails
    const month = dateObj.getMonth() + 1;
    const day = dateObj.getDate();
    return `${month}/${day}`;
  }
};

/**
 * Format numbers with commas
 */
const formatNumber = (num) => {
  if (num === null || num === undefined) return '0';
  return num.toLocaleString();
};

// Process chart data from API to format labels properly
const chartData = computed(() => {
  const rawChartData = analytics.value?.chart_data?.data;

  if (
    !rawChartData ||
    !rawChartData.labels ||
    !Array.isArray(rawChartData.labels)
  ) {
    return null;
  }

  // Format the labels using our date formatting function
  const formattedLabels = rawChartData.labels.map((label) =>
    formatDateLabel(label)
  );

  return {
    ...rawChartData,
    labels: formattedLabels,
  };
});

// Chart options with custom tooltip
const chartOptions = computed(() => ({
  responsive: true,
  maintainAspectRatio: false,
  plugins: {
    legend: {
      display: false,
      position: 'top',
      labels: {
        usePointStyle: true,
        padding: 20,
        color: 'rgb(113, 113, 122)', // zinc-500
        font: {
          size: 12,
        },
      },
    },
    tooltip: {
      enabled: false, // Disable default tooltip
      position: 'nearest',
      external: externalTooltipHandler, // Use custom tooltip
    },
  },
  scales: {
    x: {
      display: true,
      grid: {
        display: false,
      },
      ticks: {
        color: 'rgb(113, 113, 122)',
        font: {
          size: 11,
        },
        maxTicksLimit: 8,
      },
    },
    y: {
      display: true,
      beginAtZero: true,
      grid: {
        color: 'rgba(113, 113, 122, 0.1)',
        drawBorder: false,
      },
      ticks: {
        color: 'rgb(113, 113, 122)',
        font: {
          size: 11,
        },
        callback: function (value) {
          return Number.isInteger(value) ? value.toLocaleString() : null;
        },
      },
    },
  },
  elements: {
    point: {
      radius: 3,
      hoverRadius: 5,
      borderWidth: 2,
    },
    line: {
      borderWidth: 2,
    },
  },
  interaction: {
    intersect: false,
    mode: 'index',
  },
}));

/**
 * Custom tooltip handler
 */
const externalTooltipHandler = (context) => {
  // Tooltip Element
  const { chart, tooltip } = context;
  const tooltipEl = getOrCreateTooltip(chart);

  // Hide if no tooltip
  if (tooltip.opacity === 0) {
    tooltipEl.style.opacity = 0;
    return;
  }

  // Set Text
  if (tooltip.body) {
    const titleLines = tooltip.title || [];
    const bodyLines = tooltip.body.map((b) => b.lines);

    const tableHead = document.createElement('div');
    tableHead.className =
      'text-xs font-medium text-zinc-400 dark:text-zinc-500 mb-2';

    titleLines.forEach((title) => {
      const span = document.createElement('span');
      span.textContent = title;
      tableHead.appendChild(span);
    });

    const tableBody = document.createElement('div');
    tableBody.className = 'space-y-1.5';

    bodyLines.forEach((body, i) => {
      const colors = tooltip.labelColors[i];

      const row = document.createElement('div');
      row.className = 'flex items-center gap-2';

      // Color indicator
      const colorBox = document.createElement('span');
      colorBox.className = 'w-2 h-2 rounded-full flex-shrink-0';
      colorBox.style.backgroundColor = colors.backgroundColor;

      // Label and value
      const text = document.createElement('span');
      text.className = 'text-sm font-medium text-zinc-900 dark:text-zinc-100';
      text.textContent = body;

      row.appendChild(colorBox);
      row.appendChild(text);
      tableBody.appendChild(row);
    });

    const tableRoot = tooltipEl.querySelector('div');

    // Remove old children
    while (tableRoot.firstChild) {
      tableRoot.firstChild.remove();
    }

    // Add new children
    tableRoot.appendChild(tableHead);
    tableRoot.appendChild(tableBody);
  }

  const { offsetLeft: positionX, offsetTop: positionY } = chart.canvas;

  // Display, position, and set styles for font
  tooltipEl.style.opacity = 1;
  tooltipEl.style.left = positionX + tooltip.caretX + 'px';
  tooltipEl.style.top = positionY + tooltip.caretY + 'px';
  tooltipEl.style.font = tooltip.options.bodyFont.string;
  tooltipEl.style.padding =
    tooltip.options.padding + 'px ' + tooltip.options.padding + 'px';
};

/**
 * Get or create tooltip element
 */
const getOrCreateTooltip = (chart) => {
  let tooltipEl = chart.canvas.parentNode.querySelector('div.chartjs-tooltip');

  if (!tooltipEl) {
    tooltipEl = document.createElement('div');
    tooltipEl.className = 'chartjs-tooltip';
    tooltipEl.style.cssText = `
      background: rgba(255, 255, 255, 0.98);
      border: 1px solid rgba(228, 228, 231, 0.8);
      border-radius: 12px;
      color: #18181b;
      opacity: 0;
      pointer-events: none;
      position: absolute;
      transform: translate(-50%, -120%);
      transition: all 0.2s ease;
      padding: 12px 14px;
      box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.05), 0 2px 4px -2px rgb(0 0 0 / 0.05);
      backdrop-filter: blur(8px);
      z-index: 1000;
    `;

    // Dark mode styles
    const isDark = document.documentElement.classList.contains('dark');
    if (isDark) {
      tooltipEl.style.background = 'rgba(24, 24, 27, 0.98)';
      tooltipEl.style.borderColor = 'rgba(63, 63, 70, 0.8)';
      tooltipEl.style.color = '#fafafa';
    }

    const div = document.createElement('div');
    tooltipEl.appendChild(div);
    chart.canvas.parentNode.appendChild(tooltipEl);
  }

  return tooltipEl;
};

// Watch for date range changes
watch(
  () => props.dateRange,
  () => {
    if (!props.dateRange) return;
    loadAnalytics();
  },
  { deep: true, immediate: true }
);
</script>

<template>
  <div
    class="bg-zinc-50 dark:bg-zinc-800/20 rounded-3xl p-6 h-full flex flex-col border border-zinc-200/40 dark:border-zinc-800/60 pb-3 flex flex-col h-full"
  >
    <!-- Loading State -->
    <div v-if="loading" class="flex-1 flex items-center justify-center">
      <div class="text-center">
        <div
          class="w-8 h-8 border-2 border-zinc-300 dark:border-zinc-600 border-t-indigo-600 rounded-full animate-spin mx-auto mb-3"
        ></div>
        <p class="text-sm text-zinc-500 dark:text-zinc-400">
          {{ __('Loading analytics...', 'uixpress') }}
        </p>
      </div>
    </div>

    <!-- Error State -->
    <div v-else-if="error" class="flex-1 flex items-center justify-center">
      <div class="text-center">
        <AppIcon icon="error" class="text-4xl text-red-500 mx-auto mb-3" />
        <p class="text-sm text-red-600 dark:text-red-400 mb-2">
          {{ __('Failed to load analytics', 'uixpress') }}
        </p>
        <button
          @click="loadAnalytics"
          class="text-xs text-indigo-600 dark:text-indigo-400 hover:text-indigo-700 dark:hover:text-indigo-300 transition-colors"
        >
          {{ __('Try again', 'uixpress') }}
        </button>
      </div>
    </div>

    <!-- Analytics Data -->
    <div v-else-if="analytics" class="h-full grow flex flex-col gap-4">
      <!-- Header -->
      <div class="flex items-center justify-between mb-6">
        <div class="flex items-center gap-3">
          <div>
            <h3 class="text-lg font-semibold text-zinc-900 dark:text-zinc-100">
              {{ __('Page Views Analytics', 'uixpress') }}
            </h3>
            <p class="text-sm text-zinc-500 dark:text-zinc-400">
              {{ __('Website traffic trends', 'uixpress') }}
            </p>
          </div>
        </div>
      </div>

      <!-- Chart -->
      <div
        v-if="chartData"
        class="bg-white dark:bg-zinc-950/40 rounded-3xl p-4 -mx-3 grow"
      >
        <div class="h-full">
          <Line
            :key="chartKey"
            ref="chartRef"
            :data="chartData"
            :options="chartOptions"
          />
        </div>
      </div>
    </div>

    <!-- Empty State -->
    <div v-else class="flex-1 flex items-center justify-center">
      <div class="text-center">
        <AppIcon icon="analytics" class="text-4xl text-zinc-400 mx-auto mb-3" />
        <p class="text-sm text-zinc-500 dark:text-zinc-400">
          {{ __('No analytics data available', 'uixpress') }}
        </p>
      </div>
    </div>
  </div>
</template>
