<script setup>
import { ref, computed, onMounted, watch, nextTick } from 'vue';
import { Line } from 'vue-chartjs';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler,
} from 'chart.js';
import AppIcon from '@/components/utility/icons/index.vue';
import { lmnFetch } from '@/assets/js/functions/lmnFetch.js';

// Register Chart.js components
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler
);

const props = defineProps({
  dateRange: {
    type: Object,
    required: true,
  },
  appData: {
    type: Object,
    required: true,
  },
});

// Refs
const analytics = ref(null);
const loading = ref(false);
const error = ref(null);
const chartRef = ref(null);
const chartKey = ref(0);

/**
 * Load user analytics data
 */
const loadAnalytics = async () => {
  loading.value = true;
  error.value = null;

  try {
    const response = await lmnFetch({
      endpoint: 'uixpress/v1/user-analytics',
      type: 'GET',
      params: {
        start_date: props.dateRange[0].toISOString(),
        end_date: props.dateRange[1].toISOString(),
      },
    });

    analytics.value = response.data;

    // Force chart re-render by updating the key
    chartKey.value++;
  } catch (err) {
    error.value = err.message || 'Failed to load user analytics';
    console.error('User analytics error:', err);
  } finally {
    loading.value = false;
  }
};

// Computed properties
const totalUsers = computed(() => analytics.value?.total_users || 0);
const usersInRange = computed(() => analytics.value?.users_in_range || 0);
const recentUsers = computed(() => analytics.value?.recent_users || 0);
const chartData = computed(() => analytics.value?.chart_data || null);
const userRoles = computed(() => analytics.value?.user_roles || []);

// Chart options
const chartOptions = computed(() => ({
  responsive: true,
  maintainAspectRatio: false,
  plugins: {
    legend: {
      display: false,
    },
    tooltip: {
      backgroundColor: 'rgba(0, 0, 0, 0.8)',
      titleColor: 'white',
      bodyColor: 'white',
      borderColor: 'rgba(255, 255, 255, 0.1)',
      borderWidth: 1,
      cornerRadius: 8,
      displayColors: false,
      callbacks: {
        title: (context) => {
          return context[0].label;
        },
        label: (context) => {
          return `${context.parsed.y} new users`;
        },
      },
    },
  },
  scales: {
    x: {
      display: true,
      grid: {
        display: false,
      },
      ticks: {
        color: 'rgb(113, 113, 122)', // zinc-500
        font: {
          size: 12,
        },
      },
    },
    y: {
      display: true,
      beginAtZero: true,
      grid: {
        color: 'rgba(113, 113, 122, 0.1)', // zinc-500 with opacity
        drawBorder: false,
      },
      ticks: {
        color: 'rgb(113, 113, 122)', // zinc-500
        font: {
          size: 12,
        },
        callback: function (value) {
          return Number.isInteger(value) ? value : null;
        },
      },
    },
  },
  elements: {
    point: {
      radius: 4,
      hoverRadius: 6,
      backgroundColor: 'rgb(99, 102, 241)', // indigo-500
      borderColor: 'white',
      borderWidth: 2,
    },
    line: {
      borderWidth: 2,
      tension: 0.4,
    },
  },
  interaction: {
    intersect: false,
    mode: 'index',
  },
}));

// Watch for date range changes
watch(
  () => props.dateRange,
  () => {
    if (!props.dateRange) return;
    loadAnalytics();
  },
  { deep: true, immediate: true }
);
</script>

<template>
  <div
    class="bg-zinc-50 dark:bg-zinc-800/20 rounded-3xl p-6 h-full flex flex-col border border-zinc-200/40 dark:border-zinc-800/60 pb-2"
  >
    <!-- Loading State -->
    <div v-if="loading" class="flex-1 flex items-center justify-center">
      <div class="text-center">
        <div
          class="w-8 h-8 border-2 border-zinc-300 dark:border-zinc-600 border-t-indigo-600 rounded-full animate-spin mx-auto mb-3"
        ></div>
        <p class="text-sm text-zinc-500 dark:text-zinc-400">
          {{ __('Loading user analytics...', 'uixpress') }}
        </p>
      </div>
    </div>

    <!-- Error State -->
    <div v-else-if="error" class="flex-1 flex items-center justify-center">
      <div class="text-center">
        <AppIcon icon="error" class="text-4xl text-red-500 mx-auto mb-3" />
        <p class="text-sm text-red-600 dark:text-red-400 mb-2">
          {{ __('Failed to load user analytics', 'uixpress') }}
        </p>
        <button
          @click="loadAnalytics"
          class="text-xs text-indigo-600 dark:text-indigo-400 hover:text-indigo-700 dark:hover:text-indigo-300 transition-colors"
        >
          {{ __('Try again', 'uixpress') }}
        </button>
      </div>
    </div>

    <!-- Analytics Data -->
    <div v-else-if="analytics" class="space-y-4">
      <!-- Header -->
      <div class="flex items-center justify-between">
        <div class="flex items-center gap-3">
          <div>
            <h3 class="text-lg font-semibold text-zinc-900 dark:text-zinc-100">
              {{ __('User Analytics', 'uixpress') }}
            </h3>
            <p class="text-sm text-zinc-500 dark:text-zinc-400">
              {{ __('User registration trends', 'uixpress') }}
            </p>
          </div>
        </div>
      </div>

      <!-- Stats Overview -->
      <div class="grid grid-cols-3 gap-4 py-4">
        <div class="text-center">
          <div class="text-2xl font-bold text-zinc-900 dark:text-zinc-100">
            {{ totalUsers.toLocaleString() }}
          </div>
          <div class="text-sm text-zinc-500 dark:text-zinc-400">
            {{ __('Total Users', 'uixpress') }}
          </div>
        </div>

        <div class="text-center">
          <div class="text-2xl font-bold text-zinc-900 dark:text-zinc-100">
            {{ usersInRange.toLocaleString() }}
          </div>
          <div class="text-sm text-zinc-500 dark:text-zinc-400">
            {{ __('In Range', 'uixpress') }}
          </div>
        </div>

        <div class="text-center">
          <div class="text-2xl font-bold text-zinc-900 dark:text-zinc-100">
            {{ recentUsers.toLocaleString() }}
          </div>
          <div class="text-sm text-zinc-500 dark:text-zinc-400">
            {{ __('Recent (7d)', 'uixpress') }}
          </div>
        </div>
      </div>

      <!-- Chart -->
      <div
        v-if="chartData"
        class="bg-white dark:bg-zinc-950/40 rounded-xl p-4 -mx-3 -mb-3 border border-zinc-200/40 dark:border-zinc-700/20"
      >
        <div class="h-48">
          <Line
            :key="chartKey"
            ref="chartRef"
            :data="chartData"
            :options="chartOptions"
          />
        </div>
      </div>
    </div>

    <!-- Empty State -->
    <div v-else class="flex-1 flex items-center justify-center">
      <div class="text-center">
        <AppIcon icon="people" class="text-4xl text-zinc-400 mx-auto mb-3" />
        <p class="text-sm text-zinc-500 dark:text-zinc-400">
          {{ __('No user data available', 'uixpress') }}
        </p>
      </div>
    </div>
  </div>
</template>
